<template>
  <div class="list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">配置文件</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-input mr-12">
            <Input class="filter-input"
                   v-model="search.type"
                   :maxlength="maxlength"
                   placeholder="请输入设备类型"
                   @on-enter="loadTableData"></Input>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备名称"
                          v-model="search.name"></StatusSelect>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备编号"
                          v-model="search.code"></StatusSelect>
          </div>
          <div class="filter-btn">
            <Button type="primary"
                    @click="loadTableData"
                    :loading="pageInfo.loading">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default"
                    @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
      <custom-drawer ref="drawer"
                     :title="title"
                     :formValidate="formValidate"
                     :formList="formList"
                     v-model="formValue"
                     @confirm="confirm"></custom-drawer>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/status-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'

export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      search: {
        type: '',
        name: '',
        code: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [
        {
          deviceName: '11111',
          typeName: '2222',
          deviceCode: '3344',
          configType: '5555',
          configName: '666',
          configVersion: '1.0',
          updateTime: '2021-9-18'
        },
        {
          deviceName: '11111',
          typeName: '2222',
          deviceCode: '3344',
          configType: '5555',
          configName: '666',
          configVersion: '1.0',
          updateTime: '2021-9-18'
        },
        {
          deviceName: '11111',
          typeName: '2222',
          deviceCode: '3344',
          configType: '5555',
          configName: '666',
          configVersion: '1.0',
          updateTime: '2021-9-18'
        },
        {
          deviceName: '11111',
          typeName: '2222',
          deviceCode: '3344',
          configType: '5555',
          configName: '666',
          configVersion: '1.0',
          updateTime: '2021-9-18'
        }
      ],
      maxlength: 50,
      tableColumns: [
        {
          title: '设备名称',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '设备类型',
          key: 'typeName',
          align: 'left'
        },
        {
          title: '设别编码',
          key: 'deviceCode',
          align: 'left'
        },
        {
          title: '控制程序类型',
          key: 'configType',
          align: 'left'
        },
        {
          title: '控制程序名称',
          key: 'configName',
          align: 'left'
        },
        {
          title: '控制程序版本',
          key: 'configVersion',
          align: 'left'
        },
        {
          title: '跟新时间',
          key: 'updateTime',
          align: 'left'
        },
        {
          title: '操作',
          key: 'action',
          width: 220,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0
                  },
                  on: {
                    click: () => {
                      this.editInfo(params)
                    }
                  }
                },
                '文件上传并下发'
              ),
              // this.$hasAccess("devicegroup.del") &&
              h(
                'span',
                {
                  class: 'primary',
                  on: {
                    click: () => {
                      // console.log(params)
                      this.deleteInfo(params.row)
                    }
                  }
                },
                '编辑'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      formList: [],
      formValue: {},
      type: ''
    }
  },
  mounted () {
    console.log(this.$t_p('device_name'))
  },
  methods: {
    loadTableData () {},
    showDrawer (type) {
      this.type = type
      this.$refs.drawer.open()
    },
    onRowClick () {},
    onPaginationChange () {},
    onPaginationSizeChange () {},
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      } else {

      }
    },
    confirm () {}
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient';
@size-padding: 10px;

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



